.gallery {
    display: flex;
    flex-direction: column;
    margin-bottom: 150px;
}

.gallery__heading-mini {
    margin-top: 0px;
    margin-bottom: 15px;
    font-size: 21px;
    line-height: 140%;
    text-align: center;
    color: var(--accent-color);
}

.gallery__heading {
    margin-top: 0px;
    margin-bottom: 60px;
    font-family: 'Noto Serif', Georgia, serif;
    font-weight: bold;
    font-size: 30px;
    line-height: 41px;
    text-align: center;
    color: var(--accent-color);
}

.gallery__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.gallery__video {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 555px;
    height: 300px;
}

.gallery__image {
    height: 100%;
}

.gallery__playbutton {
    position: absolute;
    fill: var(--white-color);
}

.gallery__playbutton:hover {
    transform: scale(1.07);
}

.gallery__gradient {
    position: absolute;
    background: linear-gradient(0deg, rgba(51, 51, 51, 0.4), rgba(51, 51, 51, 0.4));
    width: 555px;
    height: 300px;
}

.gallery__picture {
    background-color: var(--hard-gray-color);
    height: 300px;
}